import React from 'react';
import '../CollegesCard/college-card.scss';
import collegeImg from '../../../../Assets/Images/college_card_img.png';
import { VisibilityContext } from "react-horizontal-scrolling-menu";

const CollegeCard = (props) => {
    const { itemId, onCourseSelection } = props;
    const visibility = React.useContext(VisibilityContext)

    return (
        <>
            <div onClick={() => onCourseSelection(itemId)} className="college-card-wrapper-2" id={itemId}>
                <div className="college-card-head">
                    <img src={collegeImg}></img>
                </div>
                <div className="college-card-footer">
                    <div>
                        <h4 className="font-bold font-m">IIM Bangalore</h4>
                        <p className="font-xs c-grey mt-1">Bangalore</p>
                    </div>

                    <div className="college-card-end mt-3">
                        <p>PGP | Fees INR 23k</p> <span>GMAT/ CAT</span>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CollegeCard;