import React from 'react';
import bgyellow from '../../../Assets/SVG/bgYellow.svg';
import MyPreferences from './MyPreferences';
import Carousel from 'react-material-ui-carousel';
import { Paper, Button } from '@material-ui/core';
import HorizontalScroll from '../../Shared/HorizontalScroll/horizontalScroll';


function Item(props) {
    return (
        <Paper>
            {/* <h2>{props.item.name}</h2>
            <p>{props.item.description}</p> */}
            <img src={props.item.image}></img>
            {/* <Button className="CheckButton">
                Check it out!
            </Button> */}
        </Paper>
    )
}

const LandingPageContainer = (props) => {


    var items = [
        {
            name: "Random Name #1",
            description: "Probably the most random thing you have ever seen!",
            image: bgyellow
        },
        {
            name: "Random Name #2",
            description: "Hello World!",
            image: bgyellow
        }
    ]
    let ary = [1, 2, 3, 4, 5, 4, 23, 2]

    return (
        <>
            <div className="landing-wrapper">
                <div className="greeting-text m-4">
                    <h1 className="font-bold font-xl">{props.greeting}</h1>
                    <h3 className="font-bold font-l">Welcome to Outlearn</h3>
                </div>

                <div className="landing-carousel">
                    <Carousel>
                        {
                            items.map((item, i) => <Item key={i} item={item} />)
                        }
                    </Carousel>

                </div>
                <MyPreferences />

                {/* Library code */}
                 <div className="block-wrapper mt-5 pt-2">
                    <div className="horizontal-head mb-1"><h2 className="m-2">Webinars</h2> <h3 className="m-2 pointer">View All</h3></div>
                    <HorizontalScroll cardType='webinars' />
                 </div>


                 <div className="block-wrapper mt-5 pt-2">
                    <div className="horizontal-head mb-1"><h2 className="m-2">Courses</h2> <h3 onClick={() => window.location.pathname='/courses/it'} className="m-2 pointer">View All</h3></div>
                    <HorizontalScroll cardType='courses' />
                 </div>

                 <div className="block-wrapper mt-5 pt-2">
                    <div className="horizontal-head mb-1"><h2 className="m-2">Videos to learn</h2> </div>
                    <HorizontalScroll cardType='videos' />
                 </div>

                 <div className="block-wrapper mt-5 pt-2">
                    <div className="horizontal-head mb-1"><h2 className="m-2">Articles on Information Technology</h2> </div>
                    <HorizontalScroll cardType='articles' />
                 </div>

                 <div className="block-wrapper mt-5 pt-2">
                    <div className="horizontal-head mb-1"><h2 className="m-2">Career Stories</h2> </div>
                    <HorizontalScroll cardType='story' />
                 </div>

            </div>


        </>
    )
}


export default LandingPageContainer;