import React, { useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import '../../../Courses/courses.scss';
import SubNavbar from '../../../../Header/SubNavbar';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';
import Button from '@material-ui/core/Button';
import InputBase from '@material-ui/core/InputBase';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import CoursesCardPage from '../../../../Shared/Cards/CourseCard/courses-card';
import { Link, useLocation } from 'react-router-dom';
import SwipeableTemporaryDrawer from '../../../../Shared/muiDrawer/muiDrawer';

const BootstrapInput = withStyles((theme) => ({
    root: {
        'label + &': {
            // marginTop: theme.spacing(1),
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 26px 10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        // Use the system font instead of the default Roboto font.
        fontFamily: [
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        '&:focus': {
            borderRadius: 4,
            borderColor: '#80bdff',
            boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
        },
    },
}))(InputBase);

const marks = [
    {
        value: 0,
        label: '0',
    },
    {
        value: 20,
        label: '2k',
    },
    {
        value: 40,
        label: '4k',
    },
    {
        value: 60,
        label: '6k',
    },
    {
        value: 80,
        label: '8k',
    },
    {
        value: 100,
        label: '10k+',
    },
];

const useStyles = makeStyles((theme) => ({
    margin: {
        width: 180
    },
}));

function valuetext(value) {
    return `${value}C`;
}

const CoursesIT = () => {
    let location = useLocation();
    useEffect(() => {
        console.log(location);
    }, [location])

    const greeting = "Hi Ankita";
    let ary = [1, 2, 3, 4, 5, 4]

    const classes = useStyles();

    const [age, setAge] = React.useState('');
    const handleChange = (event) => {
        setAge(event.target.value);
    };
    const [value, setValue] = React.useState([20, 37]);

    const handleChangeCost = (event, newValue) => {
        setValue(newValue);
    };

    function onCourseSelection(itemId){
        console.log(itemId);
        window.location.pathname = `${window.location.pathname}/${itemId}`;
    }

    return (
        <>
            <div className="bg-white pb-5 scrollable">
                <SubNavbar />
                
                <div className="courses-wrapper landing-wrapper-courses row">
                    <div className="heading-section flexbox-space p-0"><h1 className="font-bold ml-3">Courses</h1> <div className='mobile'><SwipeableTemporaryDrawer /></div></div>
                    <div className="courses-navbar mt-5">
                        <ul className='p-0'>
                            <li className={location.pathname=='/courses/it'?'active':''}><Link to="/courses/it">IT</Link></li>
                            <li className={location.pathname=='/courses/non-it'?'active':''}><Link to="/courses/non-it">Non IT</Link></li>
                            <li className={location.pathname=='/courses/general'?'active':''}><Link to="/courses/general">General</Link></li>
                        </ul>
                    </div>
                    <div className="row container mt-5">
                        <div className="col-md-3 desktop">
                            <div className="left-filters bg-light-violet">
                                <h6 className="font-bold c-grey">Filters</h6>
                                <div className="section">
                                    <h6 className="font-bold">Level</h6>
                                    <div className="btn-wrap">
                                        <Button variant="outlined">Beginner</Button>
                                        <Button variant="outlined">Intermediate</Button>
                                        <Button variant="outlined">Expert</Button>
                                    </div>
                                </div>

                                <div className="section">
                                    <h6 className="font-bold">Certification</h6>
                                    <div className="btn-wrap">
                                        <Button variant="outlined">All</Button>
                                        <Button variant="outlined">No Certificate</Button>
                                        <Button variant="outlined">Certificate</Button>
                                    </div>
                                </div>

                                <div className="section">
                                    <FormControl className={classes.margin}>
                                        <InputLabel htmlFor="demo-customized-select-native">Enter your preferred duration</InputLabel>
                                        <NativeSelect
                                            id="demo-customized-select-native"
                                            value={age}
                                            onChange={handleChange}
                                            input={<BootstrapInput />}
                                        >
                                            <option aria-label="None" value="" />
                                            <option value={10}>Ten</option>
                                            <option value={20}>Twenty</option>
                                            <option value={30}>Thirty</option>
                                        </NativeSelect>
                                    </FormControl>
                                </div>
                                <div className="section">
                                    <Typography id="range-slider" gutterBottom>
                                        Cost
                                    </Typography>
                                    <Slider
                                        value={value}
                                        onChange={handleChangeCost}
                                        valueLabelDisplay="auto"
                                        aria-labelledby="range-slider"
                                        getAriaValueText={valuetext}
                                        marks={marks}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9 ml-5">
                            <h4 className="font-bold m-2">Courses for you</h4>

                            <div className="courses-loop mt-3 mb-5">
                                {
                                    ary.map((element,index) => (
                                        <div className="mb-4">
                                            <CoursesCardPage onCourseSelection={onCourseSelection} itemId={index}/>
                                        </div>
                                    ))
                                }
                            </div>


                            <h4 className="font-bold m-2">Popular</h4>
                            <div className="courses-loop mt-3">
                                {
                                    ary.map((element,index) => (
                                        <div className="mb-4">
                                            <CoursesCardPage onCourseSelection={onCourseSelection} itemId={index}/>
                                        </div>
                                    ))
                                }
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CoursesIT;