import React from 'react';
import '../CourseCard/courses-card.scss';
import courseImg from '../../../../Assets/Images/course.png';
import { VisibilityContext } from "react-horizontal-scrolling-menu";

const CoursesCardPage = (props) => {
    const { itemId, onCourseSelection } = props;
    const visibility = React.useContext(VisibilityContext)

    return (
        <>
            <div onClick={() => onCourseSelection(itemId)} className="courses-card-wrapper-2" id={itemId}>
                <div className="courses-card-head">
                    <img src={courseImg}></img>
                </div>
                <div className="courses-card-footer">
                    <div><h4 className="font-bold font-m">Android</h4>
                        <h5 className="font-bold font-s">Coursera {itemId}</h5></div>

                    <div className="courses-card-end mt-2">
                        <span>Rs 4500</span>  <p>Beginner | 5 * (42k)</p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CoursesCardPage;