import React, { useEffect, useState, useContext } from 'react';
import '../../Assets/SVG/webinar.svg';
import Home from '../../Assets/SVG/home.svg';
import { Link, useLocation } from 'react-router-dom';



const SubNavbar = () => {
    let location = useLocation();
    let ary = [1, 2, 3, 4]

    return (
        <>
            <div className="subnavbar-wrapper">
                <ul>
                    <li><Link to="/"><img src={Home}></img></Link>  </li>
                    <li className={location.pathname == '/higher-studies' ? 'active-link' : ''}><Link to="/higher-studies">Higher Studies</Link></li>
                    <li className={location.pathname.includes('government-exams') ? 'active-link' : ''}><Link to="/government-exams/upsc">Government Examinations</Link></li>
                    <li className={location.pathname == '/internships' ? 'active-link' : ''}><Link to="/internships">Internships</Link></li>
                    <li className={location.pathname == '/webinars' ? 'active-link' : ''}><Link to="/webinars">Webinars</Link></li>
                    <li className={location.pathname == '/career-ladders' ? 'active-link' : ''}><Link to="/career-ladders">Career Ladders</Link></li>


                </ul>
            </div>
        </>
    )
}

export default SubNavbar;