import React from 'react';
import './InternshipsCard.scss';
import Button from '@material-ui/core/Button';
import LocationOnOutlinedIcon from '@material-ui/icons/LocationOnOutlined';
import TrendingUpOutlinedIcon from '@material-ui/icons/TrendingUpOutlined';

const ary = [1, 2, 3];

function InternshipsCard() {

  return <>

    <div className="internship-card">
      <div className="internship-head">
        <div className="info">
          <h2 className='heading-4'>Software Testing</h2>
          <p className='para-1'>Software Testing</p>

        </div>
        <div className="apply">
          <Button variant="contained" color="primary" size="medium" className="contbutton">
            Apply
          </Button>
        </div>
      </div>
      <div className="internship-body">
        <p className='mt-4 mb-4 c-black para-1'> <LocationOnOutlinedIcon /> Work from home</p>

        <div className="skillset">
          <span className='mt-3 mb-3 c-black para-1 pr-3'> <TrendingUpOutlinedIcon /> Skillset</span>
          {ary.map(e => {
            return <span className="preference-card-sm"> Software Engineering </span>
          })}
        </div>

        <div className="points row mt-4 mb-4">
          <div className='item col-md-2'>
            <p className="para-1">
              Start
            </p>
            <p className="para-1 c-black font-bold">
              June
            </p>
          </div>
          <div className='item col-md-2'>
            <p className="para-1">
              Stipend
            </p>
            <p className="para-1 c-black font-bold">
              Rs 6000/-
            </p>
          </div>
          <div className='item col-md-2'>
            <p className="para-1">
              Duration
            </p>
            <p className="para-1 c-black font-bold">
              2 months
            </p>
          </div>
          <div className='item col-md-3'>
            <p className="para-1">
              Deadline
            </p>
            <p className="para-1 c-black font-bold">
              24 Sept
            </p>
          </div>
        </div>

      </div>
    </div>

  </>;
}

export default InternshipsCard;
