import logo from './logo.svg';
import './App.scss';
import './Assets/Fonts/font.css';
import Home from './Modules/HomeContainer/Home';
import Store from './Store/Store';
import { BrowserRouter, Route, useHistory, Redirect } from "react-router-dom";
import LandingPage from './Modules/LandingPageContainer/LandingPage';
import { MuiThemeProvider, createTheme } from '@material-ui/core/styles';
import OtpScreenLogin from './Modules/Screens/OtpVerifyPage';
import Header from './Modules/Header/Header';
import Footer from './Modules/Shared/Footer/Footer';

import CoursesPage from './Modules/Categories/Courses/courses';
import CoursesIT from './Modules/Categories/Courses/Components/IT/coursesIt';
import CoursesDetailPage from './Modules/Categories/Courses/Components/CoursesDetail/coursesDetailPage';

import GovernmentExams from './Modules/Categories/GovernmentExams/GovernmentExams';
import ExamsListing from './Modules/Categories/GovernmentExams/Components/ExamsListing';
import ExamsDetails from './Modules/Categories/GovernmentExams/Components/ExamsDetails';

import HigherStudyListing from './Modules/Categories/HigherStudies/HigherStudyListing';
import HigherStudyDetail from './Modules/Categories/HigherStudies/HigherStudyDetail';

import InternshipsListing from './Modules/Categories/Internships/InternshipsListing';
import InternshipDetail from './Modules/Categories/Internships/InternshipDetail';

import WebinarsListing from './Modules/Categories/Webinars/WebinarsListing';
import WebinarsDetail from './Modules/Categories/Webinars/WebinarsDetail';

import CareerLaddersListing from './Modules/Categories/CareerLadders/CareerLaddersListing';
import CareerLaddersDetail from './Modules/Categories/CareerLadders/CareerLaddersDetail';

// use default theme
// const theme = createTheme();

// Or Create your Own theme:
const theme = createTheme({
  palette: {
    primary: {
      main: '#5F22E0'
    }
  }
});


function App() {
  const history = useHistory();
  let isAuthenticated = localStorage.getItem('visitor_info');

  return (
    <>
      <Store>
        <MuiThemeProvider theme={theme}>
          <Header />
          <BrowserRouter>
            <Route exact strict path="/onboarding" component={Home} />
            <Route exact strict path="/verify-otp" component={Home} />
            <Route exact strict path="/login" component={Home} />
            <Route exact strict path="/" component={LandingPage} />

            <Route exact strict path="/courses" component={CoursesPage} />
            <Route exact strict path="/courses/:id" component={CoursesIT} />
            <Route exact strict path="/courses/:id/:id" component={CoursesDetailPage} />

            <Route exact strict path="/government-exams" component={GovernmentExams} />
            <Route exact strict path="/government-exams/:id" component={ExamsListing} />
            <Route exact strict path="/government-exams/:id/:id" component={ExamsDetails} />

            <Route exact strict path="/higher-studies" component={HigherStudyListing} />
            <Route exact strict path="/higher-studies/:id" component={HigherStudyDetail} />

            <Route exact strict path="/internships" component={InternshipsListing} />
            <Route exact strict path="/internships/:id" component={InternshipDetail} />

            <Route exact strict path="/webinars" component={WebinarsListing} />
            <Route exact strict path="/webinars/:id" component={WebinarsDetail} />

            <Route exact strict path="/career-ladders" component={CareerLaddersListing} />
            <Route exact strict path="/career-ladders/:id" component={CareerLaddersDetail} />


          </BrowserRouter>
          <Footer/>
        </MuiThemeProvider>
      </Store>
    </>
  );
}

export default App;
