import React from 'react';
import { VisibilityContext } from "react-horizontal-scrolling-menu";
import '../StoryCard/storyCard.scss';
import careerStoriesImg from '../../../../Assets/Images/careerStories.png';

const StoryCard = ({ itemId, size }) => {
    const visibility = React.useContext(VisibilityContext)

    return (
        <>
            <div className={size == 's' ? 'careerStories-card-wrapper careerStories-card-wrapper-s' : 'careerStories-card-wrapper'} className="careerStories-card-wrapper">
                <div className="careerStories-card-head">
                    <img src={careerStoriesImg}></img>
                </div>
                <div className="careerStories-card-footer">
                    <div>
                        <h5 className="pt-3 font-bold font-s">Approov Khatri  </h5></div>
                    <p className="pt-1 font-bold c-grey">VP Technology</p>
                    <p className="pt-1 c-black">Google</p>
                </div>
            </div>
        </>
    )
}

export default StoryCard;